#wpbody-content {

  .pmwpp-form {

    .field {

      [id^=pmwpp-] {

        @media screen and (min-width: $small-screen) {

          width: 450px;
          position: absolute;
          left: 15%;
        }

        @media screen and (max-width: $small-screen - 1) {

          display: block;
          width: 100%;
        }
      }
    }
  }

  .user-manual {

    @media screen and (min-width: $small-screen) {

      width: 40%;
    }

    @media screen and (max-width: $small-screen - 1) {

      padding: 10px;
    }
  }
}